'use client'

import { useState } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { createClient } from '@/lib/supabase/client'
import { Eye, EyeOff, Loader2, Sparkles, Mail } from 'lucide-react'
import Image from 'next/image'
import { toast } from 'sonner'

export default function LoginPage() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [showPassword, setShowPassword] = useState(false)
  const [loading, setLoading] = useState(false)
  const [resending, setResending] = useState(false)
  const [error, setError] = useState('')
  const [showResendOption, setShowResendOption] = useState(false)
  const router = useRouter()
  const supabase = createClient()

  const handleResendVerification = async () => {
    if (!email) {
      toast.error('Please enter your email address')
      return
    }
    
    setResending(true)
    try {
      const { error } = await supabase.auth.resend({
        type: 'signup',
        email: email,
      })
      
      if (error) {
        toast.error('Failed to resend', { description: error.message })
      } else {
        toast.success('Verification email sent!', { description: 'Please check your inbox' })
      }
    } catch (err) {
      toast.error('Failed to resend verification email')
    } finally {
      setResending(false)
    }
  }

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')
    setShowResendOption(false)

    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      })

      if (error) {
        setError(error.message)
        // Show resend option if email not confirmed
        if (error.message.toLowerCase().includes('email not confirmed')) {
          setShowResendOption(true)
        }
        toast.error('Login failed', { description: error.message })
        return
      }

      if (data.user) {
        const { data: platformAdmin } = await supabase
          .from('platform_admins')
          .select('*')
          .eq('user_id', data.user.id)
          .single()

        toast.success('Welcome back!', { description: 'Redirecting to dashboard...' })

        if (platformAdmin) {
          router.push('/platform-admin')
        } else {
          router.push('/dashboard')
        }
      }
    } catch (err) {
      setError('An unexpected error occurred')
      toast.error('Error', { description: 'An unexpected error occurred' })
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      {/* Animated Background */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="blob blob-1 absolute w-96 h-96 -top-48 -left-48" />
        <div className="blob blob-2 absolute w-80 h-80 bottom-0 right-0" />
        <div className="blob blob-3 absolute w-72 h-72 top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2" />
      </div>
      <div className="absolute inset-0 bg-grid opacity-30" />

      <div className="max-w-md w-full space-y-8 relative z-10">
        {/* Header */}
        <div className="text-center animate-slide-down">
          <div className="flex justify-center mb-6">
            <div className="w-20 h-20 rounded-2xl flex items-center justify-center shadow-lg hover-lift overflow-hidden">
              <Image 
                src="/logo.svg" 
                alt="InstituteTool Logo" 
                width={80} 
                height={80}
                className="object-contain"
                priority
              />
            </div>
          </div>
          <h1 className="font-heading text-4xl font-bold mb-2">
            Welcome Back
          </h1>
          <p className="text-muted-foreground text-lg">
            Sign in to your institute dashboard
          </p>
          <p className="mt-4 text-sm text-muted-foreground">
            Do not have an account?{' '}
            <Link href="/auth/signup" className="font-semibold text-primary hover:text-primary/80 transition-colors">
              Create your institute account
            </Link>
          </p>
        </div>

        <Card className="glass-card shadow-xl animate-slide-up">
          <CardHeader className="space-y-1 pb-6">
            <CardTitle className="text-2xl font-heading text-center">Sign In</CardTitle>
            <CardDescription className="text-center">
              Enter your credentials to access your dashboard
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleLogin} className="space-y-6">
              {error && (
                <div className="glass bg-destructive/10 border-l-4 border-destructive text-destructive px-4 py-3 rounded-lg text-sm animate-scale-in">
                  <p className="text-sm">{error}</p>
                  {showResendOption && (
                    <Button
                      type="button"
                      variant="link"
                      size="sm"
                      className="mt-2 p-0 h-auto text-primary"
                      onClick={handleResendVerification}
                      disabled={resending}
                    >
                      {resending ? (
                        <>
                          <Loader2 className="mr-1 h-3 w-3 animate-spin" />
                          Sending...
                        </>
                      ) : (
                        <>
                          <Mail className="mr-1 h-3 w-3" />
                          Resend verification email
                        </>
                      )}
                    </Button>
                  )}
                </div>
              )}

              <div className="space-y-2">
                <label htmlFor="email" className="block text-sm font-semibold">
                  Email Address
                </label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Enter your email address"
                  className="glass-input text-base"
                />
              </div>

              <div className="space-y-2">
                <label htmlFor="password" className="block text-sm font-semibold">
                  Password
                </label>
                <div className="relative">
                  <Input
                    id="password"
                    name="password"
                    type={showPassword ? 'text' : 'password'}
                    autoComplete="current-password"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Enter your password"
                    className="glass-input text-base pr-12"
                  />
                  <button
                    type="button"
                    className="absolute inset-y-0 right-0 pr-4 flex items-center text-muted-foreground hover:text-foreground transition-colors"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    {showPassword ? (
                      <EyeOff className="h-5 w-5" />
                    ) : (
                      <Eye className="h-5 w-5" />
                    )}
                  </button>
                </div>
              </div>

              <div className="flex items-center justify-between">
                <div className="text-sm">
                  <Link href="/auth/forgot-password" className="font-medium text-primary hover:text-primary/80 transition-colors">
                    Forgot your password?
                  </Link>
                </div>
              </div>

              <Button
                type="submit"
                disabled={loading}
                className="w-full text-base font-semibold group"
                size="lg"
              >
                {loading ? (
                  <>
                    <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                    Signing in...
                  </>
                ) : (
                  <>
                    <Sparkles className="mr-2 h-5 w-5 group-hover:animate-pulse" />
                    Sign In to Dashboard
                  </>
                )}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Footer */}
        <div className="text-center animate-slide-up stagger-3">
          <p className="text-sm text-muted-foreground">
            Secure login powered by enterprise-grade encryption
          </p>
        </div>
      </div>
    </div>
  )
}
